.example::-webkit-scrollbar {
  display: none;
}

@keyframes bgcolor {
  0% {
      background-color: #fc466a9a
  }

  30% {
      background-color: #3f5efb85
  }

  60% {
      background-color: #fc466a9a
  }

  90% {
      background-color: #3f5efb85
  }

  100% {
      background-color: #fc466a9a
  }
}

.home-i :hover{
  transform: scale(1.1);
  cursor: pointer;
}

.background-image {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  height : 20vh;
  overflow: hidden;
}

.bg-change {
  -webkit-animation: bgcolor 20s infinite ;
  animation: bgcolor 10s infinite;
  -webkit-animation-direction: alternate;
  animation-direction: alternate;
}

.rocket-hover:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.blog-body{
  max-width: 50vw;
}

@media screen and (max-width: 1400px) {
  .blog-body{
    max-width: 75vw;
  }
}

@media screen and (max-width: 900px) {
  .blog-body{
    max-width: 95vw;
  }
}